var inProcent = function (number, msInPeriod) { return (number / msInPeriod) * 100; };
export var getSliderValueRange = function (decadeDate, value, startOfPeriod, endOfPeriod) {
    if (!Array.isArray(value) || !value[0] || !value[1]) {
        return undefined;
    }
    var periodStartDateTime = startOfPeriod(decadeDate).getTime();
    var periodEndDateTime = endOfPeriod(decadeDate).getTime();
    var valueStartDateTime = value[0].getTime();
    var valueEndDateTime = value[1].getTime();
    if (valueStartDateTime > periodEndDateTime || valueEndDateTime < periodStartDateTime) {
        return undefined;
    }
    if (valueStartDateTime <= periodStartDateTime && valueEndDateTime >= periodEndDateTime) {
        return [0, 100];
    }
    var msInPeriod = periodEndDateTime - periodStartDateTime;
    var offset = Math.floor(inProcent(valueStartDateTime <= periodStartDateTime ? 0 : valueStartDateTime - periodStartDateTime, msInPeriod));
    var width = Math.ceil(valueEndDateTime >= periodEndDateTime
        ? 100 - offset
        : inProcent(valueEndDateTime -
            (valueStartDateTime <= periodStartDateTime ? periodStartDateTime : valueStartDateTime), msInPeriod));
    return [offset, width];
};
